.App {
  text-align: center;
}

@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inconsolata", monospace;
  /* background-color: aliceblue; */
}

body,
html {
  width: 100%;
  height: auto;
  background-color: aliceblue;
}

.icon-div {
  display: flex;
  justify-content: flex-end;
}

.timer-div {
  width: 80%;
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.settings-div {
  width: 80%;
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.min-text {
  font-size: 20px;
}

.time-text {
  font-size: 60px;
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.focus-btn {
  width: 130px;
  border: 1px solid lightblue;
}

.focus-btn:active {
  border: 1px solid lightblue;
  background-color: lightblue;
}

.s-break-btn {
  width: 130px;
  border: 1px solid lightblue;
}

.s-break-btn:active {
  border: 1px solid lightblue;
  background-color: lightblue;
}

.l-break-btn {
  width: 130px;

  border: 1px solid lightblue;
}

.l-break-btn:active {
  border: 1px solid lightblue;
  background-color: lightblue;
}

/* App.css */
.active {
  background-color: #007bff;
  /* Change to your desired background color */
  color: #fff;
  /* Change to your desired text color */
}



/* Settings CSS  */
.input-field {
  background-color: aliceblue;
  color: black;
}

/* Style for the input field container */
.settings-group {
  margin-bottom: 20px;
}

/* Style for the label */
.settings-group label {
  display: block;
  font-size: 16px;
  margin-bottom: 8px;
  color: #333;
  /* Text color for labels */
}

/* Style for the input field */
.settings-group input {
  width: 100%;
  /* Set the width to 100% to ensure all fields have the same width */
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  /* Border color */
  border-radius: 4px;
  transition: border-color 0.2s;
}

/* Style for the input field when focused */
.settings-group input:focus {
  border-color: #007bff;
  /* Focus border color */
  outline: none;
  /* Remove the default focus outline */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  /* Add a subtle shadow on focus */
}

/* Style for error message (optional) */
.error-message {
  color: #ff0000;
  /* Red color for error messages */
  font-size: 14px;
  margin-top: 4px;
}